header {
  display: inline-block;
  font-family: "Open Sans", sans-serif;
  margin-top: 5em;
  position: -webkit-sticky;
  position: sticky;
  text-align: left;
  top: 1.5em;
  width: 200px;
}
.nav-links{
  list-style-type: none;
  margin: 0;
  text-decoration: none;
}
.nav-link{
  border-radius: 1px;
  color: #1c1c1c;
  display: inline-block;
  margin: 0 0 1em -2em;
  padding-left: 4px;
  text-decoration: none;
  width: 100%
}
.nav-link:hover{
  color: #1d2475;
}
.nav-link:visited{
  text-decoration: line-through;
}
.nav-link--contact{
  color: #ffffff;
}
.nav-link--contact:before{
  background-color: #1d2475;
  content: "";
  position: absolute;
  height: 1.5em;
  left: 0;
  right: 0;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  z-index: -1;
}
.nav-link--contact:hover:before{
  -webkit-transform: rotateX(93deg);
          transform: rotateX(93deg);
  -webkit-transform-origin: bottom;
      -ms-transform-origin: bottom;
          transform-origin: bottom;
}

@media screen and (max-width: 500px){
  header{
    margin-top: 1em;
    position: relative;
    top: 0;
    width: 100%;
  }
}



