main{
  display: inline-block;
  margin-top: 5em;
  text-align: left;
  vertical-align: top;
  width: calc(100% - 200px);
}
h1{
  font-weight: 200;
  font-size: 1.5em;
  letter-spacing: 2px;
  margin: 0 0 1em 0;
  padding: 0;
}

@media screen and (max-width: 500px){
  main{
    width: 100%;
  }
}



